<template>
  <div class="app flex-row align-items-center">
    <div class="container">
      <b-row class="justify-content-center">
        <b-col md="8">
          <b-card-group>
            <b-card no-body class="p-4">
              <b-card-body>
                <b-form>
                  <h1>Login</h1>
                  <p class="text-muted">Sign In to your account</p>
                  <b-input-group class="mb-3">
                    <b-input-group-prepend><b-input-group-text><i class="icon-user"></i></b-input-group-text></b-input-group-prepend>
                    <b-form-input type="text" v-model="input.username" class="form-control" placeholder="Username" autocomplete="username email" />
                  </b-input-group>
                  <b-input-group class="mb-4">
                    <b-input-group-prepend><b-input-group-text><i class="icon-lock"></i></b-input-group-text></b-input-group-prepend>
                    <b-form-input type="password" v-model="input.password" class="form-control" placeholder="Password" autocomplete="current-password" />
                  </b-input-group>
                  <b-row>
                    <b-col cols="6">
                      <b-button variant="primary" class="px-4" v-on:click="login()">Login</b-button>
                    </b-col>
                    <b-col cols="6" class="text-right">
                      <b-button variant="link" class="px-0" @click="gotoForgetPass()">Forgot password?</b-button>
                    </b-col>
                  </b-row>
                </b-form>
              </b-card-body>
            </b-card>
            <b-card no-body class="text-white bg-primary py-5 d-md-down-none" style="width:44%">
              <b-card-body class="text-center">
                <div>
                  <h2>Sign up</h2>
                  <p>&nbsp;</p>
                  <b-button variant="primary" class="active mt-3" @click="gotoRegister()">Register Now!</b-button>
                </div>
              </b-card-body>
            </b-card>
          </b-card-group>
        </b-col>
      </b-row>
    </div>
  </div>
</template>

<script>

import vue from 'vue';
import axios from 'axios';
import {restapiPrefix,wprestapiPrefix} from './../variables.js';
import router from '../../router';
import VueCookies from "vue-cookies-ts";
import VueNotifications from 'vue-notifications';
import miniToastr from 'mini-toastr';

vue.use(VueCookies)

const toastTypes = {
  success: 'success',
  error: 'error',
  info: 'info',
  warn: 'warn'
}

const options = {
  success: toast,
  error: toast,
  info: toast,
  warn: toast
}

miniToastr.init({types: toastTypes})

function toast ({title, message, type, timeout, cb}) {
  return miniToastr[type](message, title, timeout, cb)
}

vue.use(VueNotifications, options)
export default {
  name: 'Login',
  data() {
      return {
          input: {
              username: "",
              password: ""
          },
          userID: ''
      }
  },
   beforeCreate: function () {   
    if(this.$route.query.id !== undefined && this.$route.query.password !== undefined)
    {
        console.log(this.$route.query.id);
        //this.userID =  this.$route.query.id;
        this.userID =  atob(this.$route.query.id);
        var pass = btoa(this.$route.query.password)
        let asksUri = restapiPrefix+'/users/setSession/'+this.$route.query.id+'/'+pass;
        this.axios.get(asksUri).then(response => {

          if (response.status === 200) {
          localStorage.user_nm = response.data[0].user_login_id;
          localStorage.user_id = response.data[0].user_id;

          // this.$session.start()
          // this.$session.set('user_nm', response.data[0].user_login_id);
          // this.$session.destroy(); 
          router.push({ name: "Dashboard"})
            
          }
          console.log(response);
        });
    }
    if(localStorage.user_id != undefined && localStorage.user_id != '0')
    {        
      router.push({ name: "Dashboard"})
    }  
    
  },
  methods: {
    login() {
          if(this.input.username != "" && this.input.password != "") 
          {            
            let password = btoa(this.input.password);            
            
            var param = {
              username: btoa(this.input.username),
              password: btoa(password)
            }

            let asksUri = restapiPrefix+'/users/loginwithform';
            //let asksUri = restapiPrefix+'/users/login/'+this.input.username+'/'+password;
            //this.axios.get(asksUri).then(response => {   
            this.axios.post(asksUri,param).then(response => {   
              if(response.data.status == "error")
              {
                 miniToastr.error(response.data.message, 'Login Error');
              }
              else{
                console.log("User infos")
                console.log(response.data[0])
                this.userID = response.data[0].user_id;
                localStorage.user_nm = response.data[0].user_login_id;
                localStorage.user_id = response.data[0].user_id;
                router.push({ name: "Dashboard"});
              }
            });
          } else 
          {
              console.log("A username and password must be present");
              miniToastr.error("Please enter valid username and password", 'Login Error');
          }
      },
     gotoRegister(){
      window.open(
              wprestapiPrefix+'/pricing/',
              '_blank' // <- This is what makes it open in a new window.
            );
     },
     gotoForgetPass(){
       router.push({ name: "ForgetPassword"})
     }
  }
}
</script>

